      width: 200px;
      position: absolute;

      padding: 20px 0;
      margin: 15px 0;

      .swiper-button-prev,
      .swiper-button-next {
        color: white;
      }

      .swiper-button-prev,
      .swiper-button-next {
        position: absolute;
        z-index: 3;
        border: 4px solid white;
        padding: 10px;
        margin: 10px;
        border-radius: 70px;
        width: 70px;
        height: 70px;

        :root {
          --swiper-navigation-size: 25px;
        }
      }
